@import 'colors';

$border: rgba(0,0,0,0.15);
$text: rgba(0,0,0,0.45);

form{
	position: relative;
	padding: 10px;
}
input, textarea{
	position: relative;
	height: 100%;
	width: 100%;
	border: none;
	outline: none;
	color: $active_secondary;
	font-weight: 500;
	font-size: 14px;
	display: block;
	transition: all .2s ease-in;
}
// wrap
.wrap-container{
	position: relative;
	height: auto;
	width: auto;
	margin: 10px;
}
.input-box{
	position: relative;
	height: auto;
	width: auto;
	margin: 20px 10px;
}
.wrap-container .input-text, .input-round .input-text, .input-box .input-text{
	border: 1px solid $border;
	border-radius: 4px;
	padding: 10px 14px;
	background: $white;
	font-weight: 500;
	font-family: "Roboto" sans-serif;
}
.wrap-container .label-text,.input-round .label-text,.input-box .label-text{
	position: absolute;
	top: 50%;
	left: 14px;
	transform: translateY(-50%);
	font-size: 16px;
	pointer-events: none;
	color: $text;
	transition: all .2s ease-in;
}
.wrap-container .input-text:focus,.input-box .input-text:focus {
	border: 1px solid $primary;
	box-shadow: 0 0 0 1px $primary;
	-webkit-box-shadow: 0 0 0 1px $primary;
}
.wrap-container .input-text:focus + .label-text,.input-box .input-text:focus + .label-text,.input-round .input-text:focus + .label-text {
	color: $primary;
}
.wrap-container > .input-text:focus + .label-text,.wrap-container > .input-text:valid + .label-text{
	top: 0px;
	left: 10px;
	font-size: 12px;
	background: $white;
	padding: 0px 3px;
	font-weight: 500;
}
.input-box > .input-text:focus + .label-text,.input-box > .input-text:valid + .label-text{
	top: -10px;
	left: 6px;
	font-size: 12px;
	background: $white;
	padding: 0px 3px;
	font-weight: 500;
}
.input-round > .input-text{
	border: 1px solid $border;
	border-radius: 4px;
	padding: 10px 14px;
	background: $white;
	border-radius: 30px;
}
.input-round > .input-text:focus + .label-text,.input-text:valid + .label-text{
	top: 0px;
	left: 16px;
	font-size: 12px;
	background: $white;
	padding: 0px 3px;
	font-weight: 500;
}

// range input
input[type=range] {
	padding: 12px 20px;
  	-webkit-appearance: none;
  	width: 100%;
  	outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  	width: 100%;
  	height: 4px;
  	cursor: pointer;
  	background: $border;
  	border-radius: 4px;
}
input[type=range]::-webkit-slider-thumb {
  	height: 16px;
  	width: 16px;
  	border-radius: 50%;
  	background: $primary;
  	cursor: pointer;
  	-webkit-appearance: none;
  	margin-top: -6px;
  	border: 1px solid transparent;
}
input[type=range]:focus::-webkit-slider-thumb {
  	background: $primary_h;
}
.range-container{
	position: relative;
	height: auto;
	width: auto;
	margin: 10px; 
}
.range-container .input-text{
	border: none;
	border-radius: 4px;
	padding: 10px 14px;
	background: $white;
	font-weight: 500;
	font-family: "Roboto" sans-serif;
}
.range-container .label-range{
	padding-left: 20px;
	color: $text;
	padding-bottom: 5px;
}
.range-container .input-text:focus {
	border: none;
	outline: none;
}

// file input
.file-container{
	display: grid;
	grid-template-columns: auto auto auto;
	margin: 10px;
	border: 1px solid $border;
	border-radius: 4px;
	box-shadow: 0 0 0 1px $white;
	-webkit-box-shadow: 0 0 0 1px $white;
}
.file-container:focus-within{
	border: 1px solid $primary;
	box-shadow: 0 0 0 1px $primary;
	-webkit-box-shadow: 0 0 0 1px $primary;
}
.file-container:focus-within .label-file{
	color: $primary_h;
}
.file-container .input-file{
	grid-column: 1 / 3;
	border-right: 1px solid $border;
}
.file-container .label-file{
	color: $text;
	padding: 10px;
}
::-webkit-file-upload-button {
  	padding: 10px 14px;
  	border-radius: 4px;
  	background-color: $white;
  	border: none;
  	color: $text;
}


// label heading

/*------checkbox----*/
[type=checkbox],[type=radio]{
	width: 16px;
	height: 16px;
	margin: 1px 5px 0px 5px;
	cursor: pointer;
	display: inline-block;
	margin-right: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.checkbox-grp, .radio-grp {
	display: inline-flex;
  	margin: 0px 10px;
  	cursor: pointer;
  	font-size: 16px;
}
.checkbox-container, .radio-container {
	display: flex;
	width: auto;
	margin: 20px 10px;
  	cursor: pointer;
  	font-size: 16px;
}
.checkbox-container[disabled],.radio-container[disabled],.checkbox-grp[disabled],.radio-grp[disabled],[type=checkbox]:disabled,[type=radio]:disabled{
	opacity: 0.6;
	cursor: no-drop;
}


// date
::-webkit-datetime-edit,::-webkit-datetime-edit-fields-wrapper,::-webkit-datetime-edit-text,::-webkit-datetime-edit-month-field,::-webkit-datetime-edit-day-field,::-webkit-datetime-edit-year-field
{
	color: $white;
}
input:focus::-webkit-datetime-edit,input:focus::-webkit-datetime-edit-fields-wrapper,input:focus::-webkit-datetime-edit-text,input:focus::-webkit-datetime-edit-month-field,input:focus::-webkit-datetime-edit-day-field,input:focus::-webkit-datetime-edit-year-field
{
	color: $active_secondary;
}
input:valid::-webkit-datetime-edit,input:valid::-webkit-datetime-edit-fields-wrapper,input:valid::-webkit-datetime-edit-text,input:valid::-webkit-datetime-edit-month-field,input:valid::-webkit-datetime-edit-day-field,input:valid::-webkit-datetime-edit-year-field
{
	color: $active_secondary;
}
// placeholder
input::-webkit-input-placeholder {
	visibility: hidden;
  	color: $muted;
  	font-weight: 500;
}
input:focus::-webkit-input-placeholder {
  	visibility: visible;
  	transition: 1s;
}
// for card footer link
input.card-inputbox::-webkit-input-placeholder {
	visibility: visible;
  	color: $muted;
  	font-weight: 500;
}
input.card-inputbox:focus::-webkit-input-placeholder {
  	visibility: visible;
  	transition: 1s;
}

// toggle button
.toggle-container{
	margin: 10px;
}
input:disabled,.toggle-container[disabled] .toggle,.toggle-container[disabled] .switch,.toggle-container[disabled] .label-toggle{
	opacity: 0.6;
	cursor: no-drop;
}
.label-toggle{
	padding: 2px 5px;
	font-size: 16px;
}
.toggle{
	position: relative;
	display: inline-block;
	width: 40px;
	height: 20px;
}
.toggle input{
	opacity: 0;
	width: 0;
	height: 0;
}
.switch{
	position: absolute;
	cursor: pointer;		
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: $active_light;
	transition: 0.4s;
	-webkit-transition: 0.4s;
	border-radius: 10px;
}
.switch:before{
	position: absolute;
	content: "";
	left: 2px;
	top: 2px;
	height: 16px;
	width: 16px;
	background-color: $white;
	transition: 0.4s;
	-webkit-transition: 0.4s;
	border-radius: 8px;
}
input:checked + .switch {
  	background-color: $primary;
}
input:checked + .switch:before {
  	transform: translateX(20px);
  	-webkit-transform: translateX(20px);
}